<template>
    <div class="list">
        <cell class="item" v-for="item in messageList" 
            :title=" item.title " :label=" item.dateTime " :key=" item.id " to="detail" >
            <img class="cell-icon" slot="icon" src="../../assets/5289.png" >
        </cell>
    </div>
</template>

<script>


import * as service from '../../services/message-center';

export default {
    data() {
        return {
            messageList: [],
            list: [],
            loading: false
        };
    },
    created() {
      this.list = Object.assign({}, this.$store.state.messageCenter.search).data;
      if (this.list.length == 0) {
        this.list = [];
        this.getMessageList();
      }
    },
    computed: {
    },
    components: {
    },
    methods: {
    	getMessageList() {
    		service.getMessagetIndex().then((res) => {
	            this.messageList = res.data.data;
	        }, (err) => {
	            console.log( err );
	        });
    	},
    	loadMore() {
            console.log("loadMore");
            this.loading = true;
    		  	this.getMessageList();
            setTimeout(() => {
              for (var i = 0; i < this.messageList.length; i++) {
                this.list.push(this.messageList[i]);
              }
              this.loading = false;
            }, 500);
          },
          toDetail(id) {
            console.log(id);
            this.$router.push('message-detail');
          }
        },
        beforeRouteLeave (to, from, next) {
            if( to.meta['groupName'] === from.meta['groupName']){
                this.$store.dispatch('updateMessageCenterSearch', this.list );
            }else{
                this.$store.dispatch('clearMessageCenterSearch');
            }
            next( true );
        }
};
</script>


<style lang="scss">

    .list
    {
        .item
        {
            img
            {
                width: 80px;
                height: 80px;
            }
        }
    }


</style>



