<template>
    <div class="detail">
        <h2>{{title}}</h2>
        <h6>{{dateTime}}</h6>
        <div class="content">{{content}}</div>
    </div>
</template>

<script>


import * as service from '../../services/message-center';

export default {
    data() {
        return {
            title: '',
            dateTime: '',
            content: ''
        };
    },
    created() {
      service.getMessagetDetail().then((res) => {
          this.title = res.data.title;
          this.dateTime = res.data.dateTime;
          this.content = res.data.content;
      }, (err) => {
          console.log( err );
      });
    },
    computed: {
    },
    components: {
    },
    methods: {
    }
};
</script>


<style lang="scss">
    .detail 
    {
        position: relative;
        padding: 30px 20px;
        height: 100%;
        background-color: #fff;

        h2,h6
        {
            padding: 10px 0px;
            text-align: center;
        }
        h6
        {
            color: #666;
        }
    }
</style>



